@font-face {
  font-style: normal;
  font-weight: 300;
  font-family: "Manrope";
  font-display: swap;
  src: local("Manrope"), url("../fonts/manrope-light.woff2") format("woff2");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Manrope";
  font-display: swap;
  src: local("Manrope"), url("../fonts/manrope-regular.woff2") format("woff2");
}
@font-face {
  font-style: normal;
  font-weight: 500;
  font-family: "Manrope";
  font-display: swap;
  src: local("Manrope"), url("../fonts/manrope-medium.woff2") format("woff2");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  scroll-behavior: smooth;
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 162.5%;
  font-family: "Manrope", "Arial", sans-serif;
  color: #484848;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #f0efe9;
}

a {
  color: #484848;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

a,
button {
  cursor: pointer;
  user-select: none;
}

a,
button,
input {
  color: inherit;
  font-size: inherit;
  font-family: "Manrope", "Arial", sans-serif;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.container {
  width: 100%;
  max-width: 1240px;
  margin: 0 auto;
  padding: 0 100px;
}
@media (max-width: 1439px) {
  .container {
    padding: 0 45px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 15px;
  }
}

.button {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 20px;
  border-radius: 30px;
  background-color: #ffffff;
  transition: background-color 300ms ease;
  border: 1px solid #316dc2;
  margin: 0;
  padding: 4px;
  padding-left: 20px;
}
.button:hover:not(:disabled) {
  background-color: #316dc2;
}
.button:hover:not(:disabled) .button__text {
  color: #ffffff;
}
.button:hover:not(:disabled) .button__icon {
  color: #316dc2;
  background-color: #ffffff;
}
.button:focus-visible:not(:disabled) {
  background-color: #316dc2;
}
.button:focus-visible:not(:disabled) .button__text {
  color: #ffffff;
}
.button:focus-visible:not(:disabled) .button__icon {
  color: #316dc2;
  background-color: #ffffff;
}
.button:active:not(:disabled) {
  background-color: #316dc2;
}
.button:active:not(:disabled) .button__text {
  color: rgba(255, 255, 255, 0.7);
}
.button:active:not(:disabled) .button__icon {
  color: rgba(49, 109, 194, 0.7);
  background-color: #ffffff;
}
.button[disabled] {
  cursor: not-allowed;
  border-color: #a9afb7;
}
.button[disabled] .button__text {
  color: #a9afb7;
}
.button[disabled] .button__icon {
  color: #ffffff;
  background-color: #a9afb7;
}

.button__text {
  color: #316dc2;
  font-size: 14px;
  font-weight: 500;
  line-height: 100%;
  letter-spacing: 0.28px;
  text-transform: uppercase;
  transition: color 300ms ease;
}

.button__icon {
  display: flex;
  justify-content: center;
  align-items: center;
  color: #ffffff;
  transition: background-color 300ms ease, color 300ms ease;
  background-color: #316dc2;
  border-radius: 50%;
  width: 46px;
  height: 46px;
}

.header {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1;
  padding-top: 20px;
}
.header::before {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 100vh;
  display: none;
  background-color: rgba(13, 29, 51, 0.4);
  opacity: 0;
  content: "";
}
.header--has-overlay::before {
  display: block;
  opacity: 1;
}

.header__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 20px;
}

.header__logo {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #ffffff;
  border-radius: 50%;
  width: 52px;
  height: 52px;
}
.header__logo:hover:not(:disabled) {
  opacity: 0.6;
}
.header__logo:focus-visible:not(:disabled) {
  opacity: 0.6;
}
.header__logo:active:not(:disabled) {
  opacity: 0.8;
}

.header__button-container {
  position: relative;
}

.header__button {
  transition: gap 200ms, background-color 200ms;
}
.header__button .header__close-icon {
  display: none;
}
.header__button .header__button-text {
  transition: color 200ms;
}
.header__button .header__button-icon {
  transition: color 200ms, background-color 200ms;
}
.header__button--active {
  gap: 172px;
  background-color: #316dc2;
}
.header__button--active .header__button-text {
  color: #ffffff;
}
.header__button--active .header__button-icon {
  color: #316dc2;
  background-color: #ffffff;
}
.header__button--active .header__open-icon {
  display: none;
}
.header__button--active .header__close-icon {
  display: block;
}

.header__navigation {
  position: absolute;
  right: 0;
  left: 0;
  top: calc(100% + 8px);
  z-index: -1;
  overflow: auto;
  opacity: 0;
  border-radius: 16px;
  background-color: #316dc2;
  transition: opacity 200ms;
  padding: 35px 25px;
  max-height: 400px;
}
.header__navigation--active {
  opacity: 1;
  z-index: 1;
}

.header__links {
  display: flex;
  flex-direction: column;
  gap: 28px;
  list-style: none;
  padding: 0;
  margin: 0;
}

.header__link {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 10px;
  color: rgba(255, 255, 255, 0.7);
  font-size: 16px;
  line-height: 100%;
  letter-spacing: 0.32px;
  text-transform: uppercase;
  transition: color 200ms;
  background-color: inherit;
  border: none;
  padding: 0;
  margin: 0;
}
.header__link:hover:not(:disabled) {
  color: #ffffff;
}
.header__link:focus-visible:not(:disabled) {
  color: #ffffff;
}

.header__inner-links {
  display: flex;
  flex-direction: column;
  gap: 16px;
  list-style: none;
  padding: 0;
  padding-top: 20px;
  padding-left: 16px;
  margin: 0;
}